<template>
  <div id="app">
    <home></home>
    <about></about>
  </div>
</template>

<script>
import Home from './Home.vue'
import About from './About.vue'

export default {
  name: 'app',
  data () {
    return {
      msg: ''
    }
  },
  components: {
    'home': Home,
    'about': About
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



